<template>
  <div class="swiper">
    <mt-swipe :auto="5000">
        <mt-swipe-item  v-for="(item,index) in bannerArr"  v-bind:key="index" >
           <router-link v-if='item.class_type=="index"'  to='./'>
            <img v-lazy='imgUrlPrefix+item.imgUrl+"@!1200"' alt="">
          </router-link>
           <router-link v-if='item.class_type=="yearGoods"'  to='./yearGoods'>
            <img v-lazy='imgUrlPrefix+item.imgUrl+"@!1200"' alt="">
          </router-link>
           <router-link v-if='item.class_type=="item"'  :to='{path:"./detail",query:{id:item.url}}'>
            <img v-lazy='imgUrlPrefix+item.imgUrl+"@!1200"' alt="">
          </router-link>
          <router-link v-else-if='item.class_type=="session"'  :to='{path:"./special",query:{sessionId:item.url}}'>
            <img v-lazy='imgUrlPrefix+item.imgUrl+"@!1200"' alt="">
          </router-link>
          <router-link v-else-if='item.class_type=="huodong"'  :to='{path:"./active",query:{id:item.url}}'>
            <img v-lazy='imgUrlPrefix+item.imgUrl+"@!1200"' alt="">
          </router-link>
          <a v-else  v-bind:href='item.url'>
            <img v-lazy='imgUrlPrefix+item.imgUrl+"@!1200"' alt="">
          </a>
        </mt-swipe-item>
      </mt-swipe>
  </div>
</template>

<script>
import Vue from 'vue'
import { Toast } from 'mint-ui';
export default {
  name: 'Swiper',
  data () { 
    return {
      bannerArr :[],
    }
  },
  created(){
    this.getData();
  },
  computed:{
      imgUrlPrefix () {
        return this.$store.state.imgUrlPrefix
      }
  },
  methods:{
    
    getData(){ 
      this.$http({
        method:'get',
        url:this.$api+'/h5/ads/query?positionId=2&pageSize=6&currentPage=1',
      }).then(res=>{
        console.log(res.data.value.values)
        res.data.value.values.forEach((item,v)=>{
          let itemLink = JSON.parse(item.data).link,
              url = '',
              class_type     = '';
          //判断scheme类型
          //商品详情页

          if(itemLink.indexOf('pinkumall://default/mall_home')!=-1){
            //首页
            class_type = 'index'
          }else if(itemLink.indexOf('pinkumall://default/huodong_home')!=-1){
            //活动首页 
            class_type = 'yearGoods'
          }else if(itemLink.indexOf('pinkumall://default/item_detail')!=-1){
            //商品详情页
            url = itemLink.split('?item_id=')[1];
            class_type = 'item'
          }else if(itemLink.indexOf('pinkumall://default/session_detail')!=-1){
            //专场详情页
            url = itemLink.split('?session_id=')[1];

           class_type = 'session'
          }else if(itemLink.indexOf('pinkumall://default/huodong_detail')!=-1){
            //活动详情页
            url = itemLink.split('?huodong_id=')[1];
            class_type = 'huodong'
          }else{
            //其他
            url = itemLink
            class_type = 'other'
          }

          this.bannerArr.push({
            'imgUrl':JSON.parse(item.data).img,
            'url':url,
            'class_type':class_type
          })
        })
        console.log(this.bannerArr)
        
      }).catch(error=>{
          let instance = Toast('网络好像有问题,请稍候再试试');
          setTimeout(() => {
            instance.close();
          }, 2000);
        })
    }
  },
  mounted(){
    // console.log(this.bannerArr)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' >

@import '../../common/css/var.scss';
.mint-swipe-items-wrap{
  height: 172px;
}
.mint-swipe-indicator.is-active{
  background: $pink;
}
img{
  width: 100%;
}
</style>








