<template>

  <div class="home">
      <!-- <Download></Download> -->
      <Header></Header>
      <div class="home_content">
          <Swiper></Swiper>
          <div class="brand">
            <h2>
              <span>- 品牌精选 -</span>
            </h2>
            <div class="brand_list" v-if="getBrandSelectData.length">
              <dl class="brand_list_detail" v-for="item in getBrandSelectData">
                  <router-link :to='{path:"./special",query:{sessionId:item.sessionId}}'>
                    <img v-if="item.banner" v-lazy='imgUrl+item.banner+"@!800"' alt="">
                    <dt>
                      <img v-lazy="imgUrl+item.brandLogo+'@!400'" alt="">
                      <div>
                        <p>{{item.title}}</p>
                        <p><span><em>{{item.minDiscountPercent | transMoney0}}</em>折起</span> <i>每满{{item.discountThresholdCent| transMoney}}减{{item.discountCent|transMoney}}上不封顶</i></p>
                      </div>
                      <p>{{(new Date((item.endTime.replace(/-/g,'/')))-new Date())|timer}}</p>
                    </dt>
                    <dd>
                      <div v-for="itit in item.items">
                        <img v-lazy="imgUrl+itit.pic+'@!400'" alt="">
                        <p>￥{{itit.priceCent | transMoney00}}</p>
                      </div>
                    </dd>
                  </router-link>
              </dl>
            </div>
          </div>
          <div class="hotGoods" ref='hotGoods'>
            <h2>
              <span>- 热卖商品 -</span>
            </h2>
            <GoodsList :GoodsData="GoodsData"></GoodsList>
          </div>
      </div>
      <Footer></Footer>
  </div>
</template>

<script>
import Unit from './common_components/Unit'
import Swiper from './common_components/Swiper'
import { Toast } from 'mint-ui';
export default {
  name: 'Home',
  data () {
    return {
      bannerArr: [],
      flag:true,
      page:1,
      bool:1,
      getBrandSelectData:[],
      GoodsData:[],
      hotGoodsFlag : true,
      hotGoodsPage :1,
      hotGoodsBool :1,
      imgUrl:''
    }
  },
  created(){
    console.log('%c观火数据','color:red;text-shadow: 2px 5px 3px pink;')
    //修改footer组件底部的状态信息
    this.$store.commit('newFooterState',{
      'home':true,
      'cart':false,
      'nianhuo':false
    });
    window.localStorage.footerState = JSON.stringify({
      'home':true,
      'cart':false,
      'nianhuo':false
    });
    //修改
    this.imgUrl = this.imgUrlPrefix ? this.imgUrlPrefix : localStorage.imgUrlPrefix;
    this.getBrandSelect(this.page);
    this.getHotGoodsData(this.hotGoodsPage)
  },
  computed:{
      imgUrlPrefix () {
        return this.$store.state.imgUrlPrefix
      }
  },
  methods:{
    //品牌精选列表
    getBrandSelect(page){
      if(this.page == this.bool){//page为true
        this.bool= false;
        this.$http({
          method:'get',
          url:this.$api+'/h5/home/session_list',
          params:{
            currentPage:page,
            pageSize:3
          }
        }).then(res=>{
          // console.log(res.data.value)
          if(res.data.value.values.length){
              // this.getBrandSelectData = res.data.value.values;
              res.data.value.values.forEach((item)=>{
                this.getBrandSelectData.push(item)
              })
              this.page++;
              this.bool = this.page;
          }else{
            this.flag = false;
          }
        })
      }
    },
    //热卖商品
    getHotGoodsData(page){

      if(this.hotGoodsPage == this.hotGoodsBool){
        this.hotGoodsBool= false;
        this.$http({
          method:'get',
          url:this.$api+'/h5/home/item_list',
          params:{
            currentPage:page,
            pageSize:8
          }
        }).then(res=>{
           console.log(res.data.value.values)
          if(res.data.value.values.length){
              res.data.value.values.forEach((item)=>{
                this.GoodsData.push(item)
              })
              this.hotGoodsPage++;
              this.hotGoodsBool = this.hotGoodsPage;
          }else{
            this.hotGoodsFlag = false;
          }
        }).catch(error=>{
          let instance = Toast('网络好像有问题,请稍候再试试');
        setTimeout(() => {
          instance.close();
        }, 2000);
        })
      }
    },
    //分页
    nextpage(){
     
      window.onload = window.onscroll = ()=>{
        let top = document.documentElement.scrollTop || document.body.scrollTop;
        //文本高度
        let textHeight = document.body.scrollHeight;
        //屏幕高度
        let clientHeight = document.documentElement.clientHeight || document.body.clientHeight;
        //热卖商品高度
        let hotGoods_height = this.$refs.hotGoods.offsetHeight;
        if (textHeight - top - clientHeight-hotGoods_height< 100 && this.flag) {
          this.getBrandSelect(this.page)
        }
        if(textHeight-top-clientHeight<300 && this.hotGoodsFlag){
          this.getHotGoodsData(this.hotGoodsPage)
        }
      }
    },
  },
  filters:{
    timer: function (time) {
      let second = parseInt(time/1000)%60,
        min    = (parseInt(time/1000)-second)/60%60,
        hour   = ((parseInt(time/1000)-second)/60-min)/60%24,
        day    = (((parseInt(time/1000)-second)/60-min)/60-hour)/24;
        if(day<1){
          return '不足1天'
        }else{
          return '还剩'+day+'天'
        }
    },
  },
  mounted(){
    this.nextpage();
  },
  components:{
    Swiper
  },
  destroyed(){
    window.onscroll=()=>{}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
  @import '../common/css/var.scss';
  .swiper-container{
    height: 172px;
    
  }
  .home_content{
    position: relative;
    top: 64px;
    -webkit-overflow-scrolling: touch;
    overflow-y:scroll; 
    padding-bottom: 50px;
    background: $backgroundColor;
    max-width: 414px;
    margin: 0 auto;
  }

  .home_content::-webkit-scrollbar{
    display: none;
  }
  .brand{
    
    margin-bottom: 10px;
    h2{
      height: 40px;
      display: flex;
      span{
        font-weight: bold;
        margin: 15px auto 10px;
        font-size:14px;
        color:$pink;
        line-height:14px;
        text-align:center;
      }
    }
    .brand_list{
      width: 335px;
      // max-width: 414px;
      margin: 0 auto;
      
      .brand_list_detail{
        background: white;
        margin-bottom: 15px;
         padding-bottom: 8px;
         box-shadow:0px 5px 15px rgba(204,205,203,0.15);
        a{
          >img{
            width: 335px;
            height: 201px;
            display: block;
          }
          width: 335px;
          background: white;
          margin-bottom: 15px;

          dt{
            display: flex;
            justify-content:space-between;
            margin-bottom: 11px;
            img{
                width: 80px;
                height: 80px;
                margin-left: 9px;
            }
            div{
              padding-left: 10px;
              
              p:nth-child(1){
                width: 174px;
                height: 18px;
                font-family: $fontFamily;
                font-size:14px;
                color:$fontColor;
                line-height:18px;
                text-align:left;
                padding-top: 22px;
                overflow: hidden;
                text-overflow:ellipsis;
                white-space: nowrap;
              }
              p:nth-child(2){
                display: flex;
                // justify-content:space-between;
                font-family:$fontFamily;
                font-size:12px;
                color:$gray;
                
                line-height:12px;
                text-align:right;
                padding-top: 9px;
                span{
                  color: $pink;
                  padding-right: 5px;
                  em{
                    font-size: 14px;
                  }
                }
                i{
                  font-family: initial;
                }
              }
            }
            >p{
              background:#f4f4f4;
              border-radius:12px 0 0 12px;
              width:54px;
              height:24px;
              font-size: 10px;
              color: #737373;
              text-align: right;
              padding-right: 5px;
              line-height: 24px; 
              box-sizing:border-box;
              margin-top: 17px;
            }
          }
          dd{
            display: flex;
            flex-wrap:wrap;
            padding:0 7px;
            width: 321px;
            div{
              width: 107px;
              box-sizing:border-box;
              border:1px solid #f4f4f4;/*no*/
              background: white;
              img{
                display: block;
                width: 105px;
                height: 105px;
              }
              p{
                height: 32px;
                line-height: 32px;
                text-align: center;
                color: $fontColor;
              }
            }
          }
        }
        
      }
    }

  }
  .hotGoods{
    background: $backgroundColor;
    h2{
      display: flex;
      font-weight: bold;
      span{
        font-size:14px;
        color:#333234;
        line-height:14px;
        text-align:center;
        margin:  15px auto;
      }
    }
    
  }
</style>









